@import url(../fontawesome/css/all.css);
:root {
  --font-size-initial: 12px;
  --font-size-lv2: 14px;
  --font-size-lv3: 16px;
  --font-size-lv4: 18px;
  --spacing-site: 2rem;
  --color-text-main: #000; }

/*
*Whitespace Reset*/
* {
  padding: 0;
  margin: 0; }

/*
*Box-sizing Fix*/
html {
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

*, *:before, *:after {
  -webkit-box-sizing: inherit;
  box-sizing: inherit; }

html {
  font: var(--font-size-initial, 12px)/1.5, "Arial", sans-serif; }

@media screen and (min-width: 500px) {
  html {
    font-size: var(--font-size-lv2, 14px); } }

@media screen and (min-width: 700px) {
  html {
    font-size: var(--font-size-lv3, 16px); } }

@media screen and (min-width: 1600px) {
  html {
    font-size: var(--font-size-lv4, 18px); } }

body, table, th, td, form, input, textarea, option, select {
  font: inherit; }

h1, h2, h3, h4, h5, h6 {
  line-height: 1.2;
  padding-bottom: 0.5em; }

h1 {
  font-size: 2.441em; }

h2 {
  font-size: 1.953em; }

h3 {
  font-size: 1.563em; }

h4 {
  font-size: 1.25em; }

h5 {
  font-size: 1em; }

h6 {
  font-size: 1em;
  font-weight: bold; }

p, ul, ol {
  padding-bottom: 1.5em; }

ul, ol {
  padding-left: 2em; }

li {
  list-style-position: inside; }

@-webkit-keyframes navitemin {
  from { }
  to {
    opacity: 1;
    -webkit-transform: scale(1) translate(0, 0) rotate(0deg);
    transform: scale(1) translate(0, 0) rotate(0deg); } }

@keyframes navitemin {
  from { }
  to {
    opacity: 1;
    -webkit-transform: scale(1) translate(0, 0) rotate(0deg);
    transform: scale(1) translate(0, 0) rotate(0deg); } }

body {
  color: var(--color-text-main, red); }

.site {
  padding: var(--spacing-site, 2em); }

.site-content {
  max-width: 60rem;
  margin: 0 auto; }

.navigation {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: none;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  background: rgba(255, 255, 255, 0.9); }

.navigation ul {
  padding: 0;
  margin: 0; }

.navigation li {
  display: block;
  list-style: none outside;
  padding: 2em 0;
  -webkit-animation: navitemin 1s 1 ease forwards;
  animation: navitemin 1s 1 ease forwards;
  opacity: 0;
  -webkit-transform: scale(0) translate(0, 3rem) rotate(720deg);
  transform: scale(0) translate(0, 3rem) rotate(720deg); }

.navigation li:nth-child(1) {
  -webkit-animation-delay: 0.15s;
  animation-delay: 0.15s; }

.navigation li:nth-child(2) {
  -webkit-animation-delay: 0.3s;
  animation-delay: 0.3s; }

.navigation li:nth-child(3) {
  -webkit-animation-delay: 0.45s;
  animation-delay: 0.45s; }

.navigation li:nth-child(4) {
  -webkit-animation-delay: 0.6s;
  animation-delay: 0.6s; }

.navigation li:nth-child(5) {
  -webkit-animation-delay: 0.75s;
  animation-delay: 0.75s; }

.navigation li:nth-child(6) {
  -webkit-animation-delay: 0.9s;
  animation-delay: 0.9s; }

.navigation a {
  display: block;
  padding: 1em 2em;
  background: black;
  text-decoration: none;
  color: white;
  -webkit-box-shadow: 0 0 1.5rem 0.5rem rgba(0, 0, 0, 0.5);
  box-shadow: 0 0 1.5rem 0.5rem rgba(0, 0, 0, 0.5); }

.navigation a:before {
  margin-right: 1em; }

#nav-main-toggle {
  display: none; }

#nav-main-toggle:checked + .site-header .navigation {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex; }

.nav-toggle {
  position: fixed;
  top: var(--spacing-site, 2rem);
  left: var(--spacing-site, 2rem);
  z-index: 1000;
  font-size: 2rem;
  padding: 0.5rem; }

#nav-toggle-right {
  left: inherit;
  right: var(--spacing-site, 2rem); }

.nav-toggle span {
  display: none; }

.nav-toggle::before {
  content: "\f150";
  -webkit-box-shadow: 0 0 0.5rem 0.1rem #684c68;
  box-shadow: 0 0 0.5rem 0.1rem #684c68;
  background: purple; }

#nav-main-toggle:checked + .site-header .nav-toggle::before {
  content: "\f057"; }

#nav-main-toggle:checked + .site-header #nav-toggle-left,
#nav-main-toggle:checked + .site-header #nav-toggle-right {
  z-index: 1010; }

.listing {
  padding: 0;
  margin: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap; }

.listing li {
  display: block;
  list-style: none outside;
  -webkit-box-flex: 0;
  -ms-flex: 0 0 31%;
  flex: 0 0 31%; }

.listing a {
  display: block;
  background: #5e0083;
  border-radius: 0.3em;
  color: white;
  -webkit-box-shadow: 0 0 0.5rem 0.1rem #684c68;
  box-shadow: 0 0 0.5rem 0.1rem #684c68;
  -webkit-transition: background 0.3s ease-in, color 0.5s ease-in;
  transition: background 0.3s ease-in, color 0.5s ease-in; }

.listing a:hover {
  background: white;
  color: black;
  -webkit-box-shadow: 0 0 4rem 1rem rgba(0, 0, 0, 0.3);
  box-shadow: 0 0 4rem 1rem rgba(0, 0, 0, 0.3); }

.listing a span {
  display: block; }

body {
  padding-top: 10rem;
  background: none; }

.site-branding {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background: -webkit-gradient(linear, left top, left bottom, color-stop(60%, white), to(rgba(255, 255, 255, 0)));
  background: linear-gradient(to bottom, white 60%, rgba(255, 255, 255, 0) 100%);
  padding: var(--spacing-site);
  padding-bottom: 4rem; }

.site-title {
  font-size: 2em;
  font-weight: bold;
  text-transform: uppercase;
  padding-bottom: 1rem;
  border-bottom: 2px solid black; }

.site-description {
  font-style: italic;
  font-family: serif;
  font-size: 110%; }
